import React, { Component } from 'react';
import Back from './Back';
import AnimatedWrapper from "./AnimatedWrapper";
import * as PIXI from 'pixi.js';
import './About.css';

class AboutComponent extends Component {
  constructor(){
    super();
    this.state = {
      class: "About anim"
    }
  }

  componentWillMount(){
    if(Math.round(PIXI.loader.progress) === 100){
      this.setState({ class: "About anim in" });
    }else{
      PIXI.loader.once('complete', ()=>{
        this.setState({ class: "About anim in" });
      })
    }
  }

  render() {
    return (
      <div className={this.state.class}>
        <Back />
        <div className="Scroll">
          <span>Hi.<br/><br/>My name is Jack Beringen and i'm a programmer from Australia.<br/>I began progamming in Visual Basic when I was around 14 doing stuff with Windows Forms, and my interests peaked from there. I've since experimented with a wide range of programming, from game programming (C++ / Blueprints) in UE4, to C# Windows Forms / WPF, to LUA. More recently i've been programming in JavaScript / NodeJS and golang.<br/><br/>You can check out some of the stuff i've done (albeit not that much, i've discarded many of my projects) on my GitHub or my portfolio if i've finished that part of the website :)</span>
        </div>
      </div>
    );
  }
}

const About = AnimatedWrapper(AboutComponent);
export default About;